
































import Switches from '@/components/switches/switches.vue'
import Scroll from '@/components/scroll/scroll.vue'
import SongList from '@/components/song-list/song-list.vue'
import NoResult from '@/components/no-result/no-result.vue'
import { Song } from '@/common/ts/song'
import { Getter, Action } from 'vuex-class'
import { PlaylistMixin } from '@/common/ts/mixin'
import { Component } from 'vue-property-decorator'

@Component({
  components: {
    Switches,
    Scroll,
    SongList,
    NoResult
  }
})
export default class UserCenter extends PlaylistMixin {
    private currentIndex = 0;
    private switches = [
      {
        name: '我喜欢的'
      },
      {
        name: '最近听的'
      }
    ]

    get noResult () {
      if (this.currentIndex === 0) {
        return !this.favoriteList.length
      } else {
        return !this.playHistory.length
      }
    }

    get noResultDesc () {
      if (this.currentIndex === 0) {
        return '暂无收藏歌曲'
      } else {
        return '你还没有听过歌曲'
      }
    }

    @Getter('favoriteList') favoriteList!: Song[];
    @Getter('playHistory') playHistory!: Song[];
    protected handlePlaylist (playlist: Song[]) {
      const bottom = playlist.length > 0 ? '60px' : '';
      (this.$refs.listWrapper as HTMLElement).style.bottom = bottom
      this.$refs.favoriteList && (this.$refs.favoriteList as any).refresh()
      this.$refs.playList && (this.$refs.playList as any).refresh()
    }

    private switchItem (index: number) {
      this.currentIndex = index
    }

    private selectSong (song: Song) {
      this.insertSong(new Song(song))
    }

    private back () {
      this.$router.back()
    }

    private random () {
      let list = this.currentIndex === 0 ? this.favoriteList : this.playHistory
      if (list.length === 0) {
        return
      }
      list = list.map((song) => {
        return new Song(song)
      })
      this.randomPlay({
        list
      })
    }

    @Action('insertSong') insertSong!: Function;
    @Action('randomPlay') randomPlay!: Function;
}
